.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
  padding: 0 40px;
}
.tabPane em {
  font-style: normal;
  width: 30%;
  text-align: right;
  display: inline-block;
}
.tabPane i {
  font-style: normal;
}
.tabPane strong {
  width: 70%;
  font-weight: 400;
  display: -webkit-inline-flex;
  display: inline-flex;
}
.tabPane p {
  margin-bottom: 20px;
}
.tabPane .maxText em {
  width: 10%;
}
em,
i {
  font-style: normal;
  margin: auto;
}
.primaryheader {
  text-align: center;
  border: 1px solid #eeeeee;
  border-bottom: 0;
}
.primaryheader h3 {
  margin-bottom: 0;
  text-align: center;
}
.primaryheader span {
  line-height: 40px;
  height: 40px;
  display: block;
}
.primaryContent {
  text-align: center;
  border: 1px solid #eeeeee;
  border-bottom: 0;
}
.primaryContent:last-child {
  border-bottom: 1px solid #eeeeee;
}
.primaryContent span {
  line-height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
}
.primaryCol {
  text-align: left;
  border-left: 1px solid #eeeeee;
}
.primaryCol i {
  line-height: 16px;
  padding: 10px;
  display: block;
}
.primaryDiv {
  max-height: 300px;
  overflow-y: auto;
  padding: 12px 20px 40px;
}
